import React, { useState, useEffect } from 'react';
import axios from 'axios';
import LoginPage from './LoginPage';
import StatusPage from './StatusPage';

function JobsProcessesPage({ name }) {
  const [jobs, setJobs] = useState([]);
  const [processes, setProcesses] = useState([]);
  const [skills, setSkills] = useState([]);
  const [skillLevels, setSkillLevels] = useState([]);
  const [questions, setQuestions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pairs, setPairs] = useState([
    { job: '', process: '', selectedQuestions: [], selectedSkills: [{ skill: '', level: '' }] }
  ]);
  const [statuses, setStatuses] = useState(null);

  useEffect(() => {
    const fetchJobsProcesses = async () => {
      try {
        setLoading(true);

        const storedJobsAndProcesses = localStorage.getItem('jobsAndProcesses');
        const storedDate = localStorage.getItem('jobsAndProcessesDate');
        let shouldFetch = true;

        if (storedJobsAndProcesses && storedDate) {
          const parsedData = JSON.parse(storedJobsAndProcesses);
          const savedDate = new Date(storedDate);
          const currentDate = new Date();
          const hoursDifference = (currentDate - savedDate) / 36e5;

          if (hoursDifference < 3 && parsedData.jobs.length > 0 && parsedData.processes.length > 0) {
            setJobs(parsedData.jobs);
            setProcesses(parsedData.processes);
            setSkills(parsedData.skills);
            setSkillLevels(parsedData.skillLevels);
            setQuestions(parsedData.questions);
            setLoading(false);
            shouldFetch = false;
          }
        }

        if (shouldFetch && name) {
          const response = await axios.post('http://localhost:3001/api/get-jobs-and-processes', { name });
          const { jobsAndProcesses } = response.data;

          setJobs(jobsAndProcesses.jobs || []);
          setProcesses(jobsAndProcesses.processes || []);
          setSkills(jobsAndProcesses.skills || []);
          setSkillLevels(jobsAndProcesses.skillLevels || []);
          setQuestions(jobsAndProcesses.questions || []);

          localStorage.setItem('jobsAndProcesses', JSON.stringify(jobsAndProcesses));
          localStorage.setItem('jobsAndProcessesDate', new Date().toISOString());
        }
      } catch (error) {
        setError('Error fetching jobs and processes');
        console.error(error);
      } finally {
        setLoading(false);
      }
    };

    fetchJobsProcesses();
  }, [name]);

  const handlePairChange = (index, field, value) => {
    const newPairs = [...pairs];
    newPairs[index][field] = value;
    setPairs(newPairs);
  };

  const handleQuestionChange = (index, selectedQuestions) => {
    const newPairs = [...pairs];
    newPairs[index].selectedQuestions = selectedQuestions;
    setPairs(newPairs);
  };

  const handleSkillChange = (pairIndex, skillIndex, field, value) => {
    const newPairs = [...pairs];
    newPairs[pairIndex].selectedSkills[skillIndex][field] = value;
    setPairs(newPairs);
  };

  const handleAddSkill = (index) => {
    const newPairs = [...pairs];
    newPairs[index].selectedSkills.push({ skill: '', level: '' });
    setPairs(newPairs);
  };

  const handleAddPair = () => {
    setPairs([...pairs, { job: '', process: '', selectedQuestions: [], selectedSkills: [{ skill: '', level: '' }] }]);
  };

  const handleSubmit = async () => {
    try {
      if (name && pairs.length > 0) {
        setLoading(true);
        const response = await axios.post('http://localhost:3001/api/create-opportunities', {
          name,
          couples: pairs.map(pair => ({
            job: pair.job,
            process: pair.process,
            selectedQuestions: pair.selectedQuestions,
            selectedSkills: pair.selectedSkills
          })),
        });
        const statuses = response.data.statuses;
        setLoading(false);
        setStatuses(statuses);
      }
    } catch (error) {
      console.error('Error processing choices', error);
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error || name === undefined) {
    return <LoginPage name={name} />;
  }

  if (statuses) {
    return <StatusPage statuses={statuses} />;
  }

  return (
    <div>
      <h1>Select Jobs and Processes</h1>
      {pairs.map((pair, index) => (
        <div 
          key={index} 
          style={{
            border: '1px solid #ccc',
            padding: '10px',
            marginBottom: '10px',
            borderRadius: '5px'
          }}
          >
          {/* Job Dropdown */}
          <select
            value={pair.job}
            onChange={(e) => handlePairChange(index, 'job', e.target.value)}
          >
            <option value="">Select Job</option>
            {jobs.map((job, i) => (
              <option key={i} value={job}>
                {job}
              </option>
            ))}
          </select>

          {/* Process Dropdown */}
          <select
            value={pair.process}
            onChange={(e) => handlePairChange(index, 'process', e.target.value)}
          >
            <option value="">Select Process</option>
            {processes.map((process, i) => (
              <option key={i} value={process.value}>
                {process.text}
              </option>
            ))}
          </select>

          {/* Show additional fields (Questions and Skills) only if job and process are selected */}
          {pair.job && pair.process && (
            <div>
              {/* Questions Dropdown (Multiple Selection) */}
              <div>
                <h4>Select Questions</h4>
                <select
                  multiple
                  value={pair.selectedQuestions}
                  onChange={(e) =>
                    handleQuestionChange(index, [...e.target.selectedOptions].map(opt => opt.value))
                  }
                >
                  {questions.map((question, questionIndex) => (
                    <option key={questionIndex} value={question}>
                      {question}
                    </option>
                  ))}
                </select>
              </div>

              {/* Skills and Skill Levels (Multiple Skills with Levels) */}
              <div>
                <h4>Select Skills and Levels</h4>
                {pair.selectedSkills.map((selectedSkill, skillIndex) => (
                  <div key={skillIndex}>
                    {/* Skill Dropdown */}
                    <select
                      value={selectedSkill.skill}
                      onChange={(e) => handleSkillChange(index, skillIndex, 'skill', e.target.value)}
                    >
                      <option value="">Select Skill</option>
                      {skills.map((skill) => (
                        <option key={skill.value} value={skill.value}>
                          {skill.text}
                        </option>
                      ))}
                    </select>

                    {/* Skill Level Dropdown */}
                    <select
                      value={selectedSkill.level}
                      onChange={(e) => handleSkillChange(index, skillIndex, 'level', e.target.value)}
                    >
                      <option value="">Select Skill Level</option>
                      {skillLevels.map((level) => (
                        <option key={level.value} value={level.value}>
                          {level.text}
                        </option>
                      ))}
                    </select>
                  </div>
                ))}
                <button onClick={() => handleAddSkill(index)}>Add Skill</button>
              </div>
            </div>
          )}
        </div>
      ))}
      <button onClick={handleAddPair}>Add Another Pair</button>
      <button onClick={handleSubmit}>Submit</button>
    </div>
  );
}

export default JobsProcessesPage;
