import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import JobsProcessesPage from '../AppUtils/CreateOpportunitiesUtils/JobsProcessesPage';
import LoginPage from '../AppUtils/CreateOpportunitiesUtils/LoginPage';
import StatusPage from '../AppUtils/CreateOpportunitiesUtils/StatusPage';
import { getAllCookies } from '../AppUtils/CreateOpportunitiesUtils/cookies'; // Import the cookie utility

function CreateOpportunities() {
  const [cookies, setCookies] = useState({});

  useEffect(() => {
    const allCookies = getAllCookies();
    setCookies(allCookies);
  }, []);

  return (
    <JobsProcessesPage name={cookies.name}/>
  );
}

export default CreateOpportunities;